.button {
    width: 100px;
    height: 50px;
    border: none;
    outline: none;
    color: #fff;
    background: #111;
    cursor: pointer;
    position: absolute;
    bottom: 90%;
    left: 90%;
    z-index: 0;
    margin-right: 30px;
    border-radius: 10px;
    font-size: 18px;
}

.button:before {
    content: '';
    background: linear-gradient(45deg, #ff0000, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, #ff0000);
    position: absolute;
    top: -2px;
    left: -2px;
    background-size: 400%;
    z-index: -1;
    filter: blur(5px);
    width: calc(100% + 4px);
    height: calc(100% + 4px);
    animation: glowing 20s linear infinite;
    opacity: 0;
    transition: opacity .3s ease-in-out;
    border-radius: 10px;
}

.button:active {
    color: #000
}

.button:active:after {
    background: transparent;
}

.button:hover:before {
    opacity: 1;
}

.button:after {
    z-index: -1;
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: #111;
    left: 0;
    top: 0;
    border-radius: 10px;
}

@keyframes glowing {
    0% {
        background-position: 0 0;
    }
    50% {
        background-position: 400% 0;
    }
    100% {
        background-position: 0 0;
    }
}


.photo {
    display: flex;
    flex-direction: row;
    margin-bottom: 20px;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.3);
    align-items: center;
    background-color: #ffffdb;
}

.photoImage {
    width: 100%;
    height: auto;
}

.photoDetails {
    padding: 10px;
}

.photoTitle {
    font-size: 20px;
    font-weight: bold;
    margin: 0;
    margin-bottom: 10px;
}

.photoDescription {
    margin: 0;
    margin-bottom: 10px;
}

.photoComments {
    margin: 0;
    padding: 0;
    list-style: none;
}

.photoComment {
    margin-bottom: 5px;
    background-color: #f5d538;
    padding: 10px;
    border-radius: 10px;
}

.photoCommentAuthor {
    font-weight: bold;
}

.photoCommentText {
    margin: 0;
}

form {
    display: flex;
    flex-direction: column;
    margin-top: 10px;
}

label {
    font-weight: bold;
    margin-bottom: 5px;
}

input {
    padding: 5px;
    margin-bottom: 10px;
}

button {
    padding: 5px 10px;
    background-color: #4CAF50;
    color: #fff;
    border: none;
    border-radius: 3px;
    cursor: pointer;
}

button:hover {
    background-color: #306d80;
}